import { useParams, Navigate } from 'react-router-dom';
import { useState, useEffect } from 'react';

import api from 'pages/Api';
import BasePage from 'pages/base_page';
import MemoryContent from 'pages/memory/MemoryContent';
import SimpleSnackbar from 'components/simple_snackbar';
import MemoryContentLoading from './MemoryContentLoading';

async function getSharedMemory(
  memoryId,
  handleMemoryCallback,
  handleMemoryError,
) {
  try {
    const memoryData = await api.viewSharedMemory(memoryId);
    if (memoryData.status) {
      handleMemoryError(memoryData);
      return;
    }
    handleMemoryCallback(memoryData);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ViewSharedMemory() {
  const params = useParams();
  const { memoryId } = params;
  const [memory, setMemory] = useState();
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);
  const [snackError, setSnackError] = useState(null);

  const handleMemory = (mem) => setMemory(mem);

  const [memError, setMemError] = useState(null);

  const handleSnackMessage = (msg, error) => {
    setSnackMessage(msg);
    setSnackError(error);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const handleMemoryError = (results) => {
    if (results.status === 'unauthenticated') {
      handleSnackMessage('You have been logged out. Navigating to login page', true);
      setSnack(true);
      setInterval(() => setMemError('unauthenticated'), 3000);
    }

    if (results.status === 'forbidden') {
      handleSnackMessage('You do not have access to this memory. Navigating to memories page', true);
      setSnack(true);
      setInterval(() => setMemError('forbidden'), 3000);
    }
  };

  useEffect(() => {
    getSharedMemory(memoryId, handleMemory, handleMemoryError);
    const interval = setInterval(
      () => getSharedMemory(memoryId, handleMemory, handleMemoryError),
      300000,
    );
    return () => clearInterval(interval);
  }, []);

  if (memError === 'unauthenticated') {
    return <Navigate to="/login" />;
  }

  if (memError === 'forbidden') {
    return <Navigate to="/memories/shared" />;
  }

  return memory ? (
    <BasePage create>
      <MemoryContent
        memType="shared"
        memTitle={memory.title}
        memDate={memory.date}
        memImages={memory.pictures.photos}
        memStory={memory.story}
        memoryID={memoryId}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
        error={snackError}
      />
    </BasePage>
  ) : (
    <BasePage create>
      <MemoryContentLoading />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
      />
    </BasePage>
  );
}
